const TaskList =({ tasks, setTasks }) =>{

    const deleteHandler = (index) =>{
        const newTasks = [...tasks]
        newTasks.splice(index, 1)
        setTasks={newTasks}
    }

    return ( 
        <div>
            {tasks.map((myTask, index) =>{
                return (
                    <div>
                        <p>{myTask}</p>
                        <button onClick={() => deleteHandler(index, 1)}>-</button>
                    </div>
                )
            })}
        </div>
        )
    }

export default TaskList  